import axios from "axios";
import { config } from "@/config";

export type Product = {
  id: string;
  name: string;
  description: string;
  price: number;
  inStock: boolean;
  imageUrl?: string;
};

export const getProducts = async (): Promise<Product[]> => {
  const response = await axios.get(`${config.apiBaseUrl}/products`);
  return response.data;
};

export const getProductById = async (id: string): Promise<Product> => {
  const response = await axios.get(`${config.apiBaseUrl}/products/${id}`);
  return response.data;
};

export const createProduct = async (product: Omit<Product, "id">, token: string): Promise<Product> => {
  const response = await axios.post(`${config.apiBaseUrl}/products`, product, {
    headers: { Authorization: `Bearer ${token}` }
  });
  return response.data;
};
